import { PagePropsFactoryOptions, PagePropsPipelineContext } from '../interfaces';
import { resolveParams } from '../routeUtils';
import { pagePropsPipeline } from './pipeline';
import { getNextConfig } from '@uniformdev/next';

export { pagePropsPipeline } from './pipeline';

export function getPageProps(options: PagePropsFactoryOptions) {
    const { resolvedRoute, resolvedLanguage, queryStringParams } = resolveParams(options.routeParams, () =>
        typeof options.env.getCurrentLanguage === 'function'
            ? options.env.getCurrentLanguage()
            : getNextConfig().defaultLanguage
    );

    const context: PagePropsPipelineContext = {
        props: {},
        ...options,
        routeParams: {
            sitecoreRoute: resolvedRoute,
            lang: resolvedLanguage,
            ...queryStringParams,
        },
    };

    return pagePropsPipeline.run(context).then((context) => context.props);
}
